@import url('https://fonts.googleapis.com/css?family=Heebo:300,400,500');
@import 'navbar', 'header', 'pitch';

body{
  background-color:#FFFFFF;
  margin:0;
  padding:0;
  height:100%;
  font-family: 'Heebo', sans-serif;
}


h1, h2 {
  font-weight: 500;
}

h3, h4, button, select{
  font-weight: 400;
}

h5{
  font-weight: 300;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1.25em;
}

h4, button, select{
  font-size: 1em;
}










